<template>
  <div>
    <noti v-ref:noti></noti>
    <!---->
    <div class="activity-head"><span class="activity-title" @click="back()"> {{data.total}}</span></div>

    <!---->
    <div class="activity-content">
      <activity-card v-for="list in lists" :item="list" @onclickpraise="onclickpraise">

      </activity-card>
      <!---->
      <infinite-loading :on-infinite="onLoadMore" v-if="data">
        <span slot="no-more">
          ...
        </span>
      </infinite-loading>
    </div>

  </div>
  <!---->
  <div class="activity-list-fail" v-if="!data&&!$loadingRouteData" @click="load()">
    <fail text=","></fail>
  </div>

  <!-- -->
  <div v-if="$loadingRouteData" class="loading-area">
    <pulse-loader color="rgb(38, 162, 255)" size="12px"></pulse-loader>
  </div>
</template>
<style scoped>
  .loading-area {
    display: flex;
    justify-content: center;;
    margin-top: 120px;
  }

  .activity-head {
    padding: 0;
    margin: 0;
    background-color: white;
    width: 100%;
    height: 60px;
    position: fixed;
    display: flex;
    align-items: center;
    padding-left: 10px;
    border-bottom: solid 1px rgba(213, 213, 213, .5);
    z-index: 200;
  }

  .activity-title {
    font-weight: bold;
  }

  .activity-content {
    position: relative;
    padding: 60px 10px 10px 10px;
  }

</style>
<script type="text/ecmascript-6">
  import ActivityCard from'components/activity-card/activity-card.vue'
  import {token,login_state,is_login,school,user_id} from '../../vuex/getters.js';
  import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
  import InfiniteLoading from 'vue-infinite-loading';
  import Noti from 'components/noti.vue';
  import Fail from 'components/fail.vue';
  import praise from '../../util/praise.js';
  export default{
    methods: {
      back(){
        window.history.back()
      },
      onLoadMore(){
        let url = `${this.$api.url_base}/activity/`;
        return this.$request
          .get(url)//GET url/activity
          .query({token: this.token})
          .query({offset: ( this.data.offset || 0) + 5, limit: 5})
          .then(this.$api.checkResult)
          .then((data)=> {
            //
            console.log(data);
            this.$broadcast('$InfiniteLoading:loaded');
//           //
            this.lists = this.lists.concat(data.items);
            this.data.offset = data.offset;
            this.data.total = data.total;
//            //
            if (this.data.offset >= this.data.total) {
              this.$broadcast('$InfiniteLoading:complete');
            }
          })
          .catch(function (e) {
            console.log(e);
          })
      },
      load(){
        return this.$request
          .get(`${this.$api.url_base}/activity/`)
          .query({token:this.token})
          .then(this.$api.ckeckResult)
          .then((res)=>{
            var data =res.body.data;
            this.data=data;
            console.log(data);
            this.url_type=data.url_type;
            this.lists=data.items;
          })
          .catch((e)=> {
            console.log("adadddasd")
            console.log(e)
            if (e.type === 'API_ERROR') {//apicheckResultcheckResult
              if (e.code === 23333) {//code,
                return this.$refs.noti.warning(``)//return   : )
              } else if (e.code === 401) {
                return this.$router.go({
                  path: '/login',
                  query: {
                    __ref: this.$route.path//login
                  }
                });
              } else {
                return this.$refs.noti.warning(`:${e.message}`)
              }
            } else {
              console.error(e.stack||e);
              return this.$refs.noti.warning(`:${e.message}`)
            }
            //
          })

      },
      onclickpraise(ext_data,id,ext_type){
        return praise.praise(ext_data, id, ext_type, this);
      },

    },
    vuex: {
      getters: {
        login_state,
        token,
        is_login,
        school,
        user_id
      }
    },
    //
    route: {
      //()
      data(){

        var self=this
        let url = `${this.$api.url_base}/activity/`;
        return this.$request
          .get(url)//GET url/activity
          .query({token: this.token})
          //            .query({ext_type: [1]})//    query   url /demand?ext_type=1&ext_type=2&ext_type=3 
          .then(this.$api.checkResult)//codedata
          .then(function (data) {

            //vue-routerdata
            return {
              data: data,
              url_type: data.url_type,
              lists: data.items
            }
          })
          .catch((e)=> {
            console.log("adadddasd")
            if (e.type === 'API_ERROR') {//apicheckResultcheckResult
              if (e.code === 23333) {//code,
                return this.$refs.noti.warning(``)//return   : )
              } else if (e.code === 401) {
                return this.$router.go({
                  path: '/login',
                  query: {
                    __ref: this.$route.path//login
                  }
                });
              } else {
                return this.$refs.noti.warning(`:${e.message}`)
              }
            } else {
              console.error(e.stack||e);
              console.log(self.$refs.noti);
              return this.$refs.noti.warning(`:${e.message}`)
            }
            //
          })
      }
    },
    data(){
      return {

        lists: [],
        data: null,
        url_type: 0
      }
    },
    components: {
      ActivityCard,
      PulseLoader,
      InfiniteLoading,
      Noti,
      Fail


    }
  }
</script>



