<template>
  <div class="nuo-list-wrapper">
    <noti v-ref:noti></noti>
    <div class="nuo-list-head">
      <div class="nuo-list-head-left" @click="back"> </div>
      <div class="nuo-list-head-right">
        <profile-icon class="icon" style="fill: white;" @click='filterMine'></profile-icon>
        <add-icon class="icon" style="fill: white;" @click="createNuo"></add-icon>
      </div>

    </div>
    <div class="nuo-list-content" v-if="!$loadingRouteData">
      <div class="nuo-card-wrapper" v-for="item in items">
        <nuo-card :data="item" @detail="detail(item)" @join="join(item)" @like="like(item)"
                  @hate="hate(item)"></nuo-card>
      </div>

      <infinite-loading :on-infinite="onLoadMore" >
        <span slot="no-more">
          ...
        </span>
      </infinite-loading>



    </div>
    <fail  v-if="fail" @click="reload"></fail>
    <div v-if="$loadingRouteData" class="loading-area">
      <pulse-loader color="#35ca6e" size="12px"></pulse-loader>
    </div>
  </div>
</template>
<style scoped src="./list.scss" lang="scss">

</style>
<script type="text/ecmascript-6">
  import ProfileIcon from 'svg/common/Profile.vue';
  import AddIcon from 'svg/main/demand/Add.vue';
  import NuoCard from 'components/nuo-card/nuo-card.vue'
  //https://github.com/greyby/vue-spinner
  import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
  //https://peachscript.github.io/vue-infinite-loading
  import InfiniteLoading from 'vue-infinite-loading';
  import _ from 'lodash'
  import {token, login_state, is_login} from '../../vuex/getters.js'
  import Fail from 'components/fail.vue';

  import Noti from 'components/noti.vue'
  export default{
    components: {
      AddIcon,
      ProfileIcon,
      NuoCard,
      PulseLoader,
      InfiniteLoading,
      Noti,
      Fail

    },
    data(){
      return {
        fail:false,
        items: [],
        offset: 0,
        count: 0
      }
    },
    vuex: {
      getters: {
        token
      }
    },
    //
    route: {
      //()
      data(){
        let mine = this.$route.query.mine === 1?1:0;//
        return this.load(0, mine);
      }
    },
    methods: {
      filterMine(){
        let mine = this.$route.query.mine === 1?1:0;//
        if(mine===1){
          this.$router.replace({path:"/nuo"});
        }else{
          this.$router.replace({path:"/nuo",query:{mine:1}});

        }
      },
      back(){
        window.history.back();
      },
      load(offset, mine = 0,isLoadMore=false){
        let token = this.token;
        let url_base = this.$api.node_api_base;
        let callback=()=>{
          if(offset===0){
            this.fail=true;
          }
        };
        return this.$request
          .get(`${url_base}/nuo`)//
          .query({token: token})//  token
          .query({offset, limit: 2})//limit8
          .query({mine:mine===0?0:1})//
          .then(this.$api.checkResult)//thencatch
          .then( (data)=> {
            this.fail=false;
            //
            this.items=this.items.concat(data.items || []);//
            _.uniqWith(this.items, (a, b)=> {//id
              return a.id === b.id;
            });
            this.count = data.count || 0;//
            this.offset = data.offset + data.items.length;//
            if(isLoadMore)
              this.$broadcast('$InfiniteLoading:loaded');//load

            if (isLoadMore&&data.has_next === false)
              this.$broadcast('$InfiniteLoading:complete');//
          })
          .catch((e)=> {

            this.$broadcast('$InfiniteLoading:loaded');
            if (e.type === 'API_ERROR') {//apicheckResultcheckResult
              if (e.code === 23333) {//code,
                return this.$refs.noti.warning(``,{timeout:1000, callback});//return   : )
              } else if (e.code === 401) {
                return this.$router.go({
                  path: '/login',
                  query: {
                    __ref: this.$route.path//login
                  }
                });
              } else {
                return this.$refs.noti.warning(`:${e.message}`,{timeout:1000, callback})
              }
            } else {
              console.error(e.stack||e);
              return this.$refs.noti.warning(`:${e.message}`,{timeout:1000, callback})
            }

            //
          })
      },
      reload(){
        let mine = this.$route.query.mine === 1?1:0;//
        return this.load(0, mine);
      },
      onLoadMore(){
        console.log('more');
        let mine = this.$route.query.mine === 1?1:0;//

        return this.load(this.offset, mine,true);

      },
      detail(item){
        this.$router.go(`/nuo/${item.id}`)
      },
      createNuo(){
        this.$router.go(`/create-nuo`)
      },
      join(item){
        let token = this.token;
        let url_base = this.$api.node_api_base;
        item.status = 1;//
        this.$request
          .post(`${url_base}/nuo/${item.id}/participator`)
          .query({token: token})
          .then(this.$api.checkResult)
          .catch(function (err) {
            console.err(err);
            item.status = 0;//
          });
      },
      like(item){

        let token = this.token;
        let url_base = this.$api.node_api_base;

        if (!item.praised) {
          let save_item = _.cloneDeep(item);//item
          item.praised = true;
          item.praise_count++;
          if (item.is_low) {
            item.is_low = false;
            item.low_count--;
          }

          this.$request
            .post(`${url_base}/nuo/${item.id}/praise`)
            .query({token: token})
            .then(this.$api.checkResult)
            .then(function (data) {
              console.log("like success");
            })
            .catch(function (err) {
              console.err(err);
              item = save_item;//item
            });
        }

      },
      hate(item){

        let token = this.token;
        let url_base = this.$api.node_api_base;

        if (!item.is_low) {
          let save_item = _.cloneDeep(item);
          item.is_low = true;
          item.low_count++;
          if (item.praised) {
            item.praised = false;
            item.praise_count--;
          }

          this.$request
            .post(`${url_base}/nuo/${item.id}/low`)
            .query({token: token})
            .then(this.$api.checkResult)
            .then(function (data) {
              console.log("hate success");
            })
            .catch(function (err) {
              console.err(err);
              item = save_item;
            });
        }
      }
    }

  }
</script>



