<template>
  <div class="url_style">
    <div class="activity-head"><span class="activity-title" @click="back()"> {{title}}</span></div>
    <div>
      <!--$loadingRouteDatatruefalse-->
      <photos-wipe v-ref:viewer></photos-wipe>
      <div v-if="!$loadingRouteData">
        <demand-card v-for="item in list" :data="item" :is_detail="false" @view-image="viewImage"
                     @click="$router.go('/demand-detail/'+item.id)"></demand-card>
        <infinite-loading :on-infinite="onLoadMore">
          <span slot="no-more">
            ...
          </span>
        </infinite-loading>
      </div>
      <div v-if="$loadingRouteData" class="loading-area">
        <pulse-loader color="rgb(38, 162, 255)" size="12px"></pulse-loader>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
  .url_style {
    /*padding: 20px 0 20px 0;*/
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
  }

  .loading-area {
    display: flex;
    justify-content: center;;
    margin-top: 120px;
  }

  .url_style .activity-head {
    flex: none;
  }

  .activity-head {
    padding: 0;
    margin: 0;
    background-color: white;
    width: 100%;
    height: 60px;
    /*position: fixed;*/
    display: flex;
    align-items: center;
    padding-left: 10px;
    border-bottom: solid 1px rgba(213, 213, 213, .5);
    z-index: 200;
  }
</style>
<script type="text/ecmascript-6">
  import DemandCard from 'components/demand-card/demand-card.vue'
  import PhotosWipe from 'components/photoswipe/photoswipe.vue'
  import {token, login_state, is_login} from '../../vuex/getters.js'
  import Vue from 'vue'
  import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
  import InfiniteLoading from 'vue-infinite-loading'
  export default{
    components: {
      DemandCard,
      PhotosWipe,
      PulseLoader,
      InfiniteLoading
    },
    methods: {
      back(){
        window.history.back()
      },
      viewImage(index, photos){
        console.log(index, photos);
        this.$refs.viewer.show(index, photos);
      },
      onLoadMore(){
        var token = this.token;
        this.$request
          .get(`${this.$api.url_base}/ask_reply`)//GET url/demand
          .query({token: token})
          .query({ext_type: 12})
          .query({creator:this.user_id})
          .query({offset: this.data.offset + 5, limit: this.data.limit})
          .then(this.$api.checkResult)
          .then((data=> {
            //
            this.$broadcast('$InfiniteLoading:loaded');
            //
            this.list = this.list.concat(data.items);
            this.data.offset = data.offset;
            this.data.total = data.total;
            //
            if (this.data.offset >= this.data.total) {
              this.$broadcast('$InfiniteLoading:complete');
            }
          }))
      }
    },
    //
    route: {
      //()
      data(){

        this.title = this.$route.query.title;
        this.url = this.$route.query.url;
        var token = this.token;

        return this.$request
          .get(`${this.$api.url_base}/demand`)//GET url/demand
          .query({token: token})
          .query({ext_type: [1, 2, 3]})//    query   url /demand?ext_type=1&ext_type=2&ext_type=3 
          .query({offset: 0, limit: 5})
          .then(this.$api.checkResult)//codedata
          .then(function (data) {
            //vue-routerdata
            return {
              data: data,
              list: data.items
            }
          })
          .catch((e)=> {
            if (e.type === 'API_ERROR') {//apicheckResultcheckResult
              if (e.code === 23333) {//code,
                return this.$refs.noti.warning(``, {
                  timeout: 1500
                })//return   : )
              } else if (e.code === 401) {
                return this.$router.go({
                  path: '/login',
                  query: {
                    __ref: this.$route.path//login
                  }
                });
              } else {
                return this.$refs.noti.warning(`:${e.message}`, {
                  timeout: 1500
                })
              }
            } else {
              console.error(e.stack || e);
              return this.$refs.noti.warning(`${e.message}`, {
                timeout: 1500
              })
            }
          })
      }
    },
    data(){
      return {
        title: '',
        url: '',
        list: [],
        data: {}
      }
    },
    props: {
      isWebView: {
        type: Boolean,
        required: false,
        default: true
      }
    },
    vuex: {
      actions: {},
      getters: {
        login_state,
        token,
        is_login
      }
    }
  }
</script>




