<template>
  <div class="login-wrapper">
    <div class="title">
      <span class="logo"></span>
      <span class="text"></span>
    </div>
    <div class="login-input-field-wrapper">
      <div class="login-input-field">
        <div class="login-input-field-username">
          <span class="input-title">&nbsp;&nbsp;</span>
          <input v-model="username" placeholder="6~16" @blur="checkUserName()">
        </div>
        <div class="login-input-field-password">
          <span class="input-title">&nbsp;&nbsp;</span>
          <input type="password" placeholder="6~16" v-model="password" @blur="checkPsw()">
        </div>
        <div class="login-input-field-password">
          <span class="input-title">&nbsp;&nbsp;</span>
          <div @click="clickTag()"><input class="school_text" readonly :value="tag || ''"></div>
          <tag-editor v-ref:tag-editor :tags.sync="tags" :tag-name.sync="tag" :tag.sync="id" :editable="!!1" :show.sync="tagShow"></tag-editor>
        </div>
        <div>

        </div>

      </div>
      <div class="btn " @click="onRegister">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
    </div>
    <div class="login-hint" v-link="{path:'/login'}">?</div>
  </div>

  <noti v-ref:noti></noti>
</template>

<style src="./register.scss" scoped lang="scss"></style>

<script>
  import TagEditor from '../../components/publish/publish-tag-editor.vue'
  import noti from '../../components/noti.vue'
  export default{
    route: {
      //()
      data(){
        var token = this.token;
        return this.$request
          .get(`${this.$api.url_base}/schools`)
          .then(this.$api.checkResult)//codedata
          .then((data)=> {
            //
            this.tags = data.items.map(function (item, index) {
              item.value = item.id;
              return item;
            });
          })
          .catch((e)=> {
            if (e.type === 'API_ERROR') {//apicheckResultcheckResult
              if (e.code === 23333) {//code,
                return this.$refs.noti.warning(``, {
                  timeout: 1500
                })//return   : )
              } else if (e.code === 401) {
                return this.$router.go({
                  path: '/login',
                  query: {
                    __ref: this.$route.path//login
                  }
                });
              } else {
                return this.$refs.noti.warning(``, {
                  timeout: 1500
                })
              }
            } else {
              console.error(e.stack || e);
              return this.$refs.noti.warning(``, {
                timeout: 2000
              })
            }
            //
          })
      }
    },
    data(){
      return {
        username:"",
        password:"",
        tag:"",
        id:"",
        tags:[],
        tagShow:false
      }

    },
    methods: {
      checkUserName(){
        var user_id = this.username;
        var self = this;
        if (user_id == ''){
          self.$refs.noti.warning("",{
            timeout:2000
          });
          return 1;
        }else if ( !(/^\w{6,16}$/).exec(user_id)){
          self.$refs.noti.warning('6~16',{
            timeout:2000
          });
          return 1;
        }
        return this.$request
          .get(`${this.$api.url_base}/check/${user_id}`)
          .then(this.$api.checkResult)//codedata
          .then(function (date) {
            if (date.is_exists == true){
               self.$refs.noti.warning('~',{
                timeout:2200,
                callback(){
                  self.username = '';
                  return 1;
                }
              });
              return 1;
            } else {
              return 0;
            }
          })
          .catch(function () {
            return 1;
          })
      },
      checkPsw(){
        var password = this.password;
        var self = this;
        if (password == ''){
          self.$refs.noti.warning("",{
            timeout:2000
          });
          return 1;
        }else if ( !(/^\w{6,16}$/).exec(password)){
          self.$refs.noti.warning('6~16',{
            timeout:2000
          });
          return 1;
        }else {
          return 0;
        }
      },
      onRegister(){
        var user_id = this.username;
        var passwd = this.password;
        var school_id = this.id;
        var self = this;
        if (user_id == ''){
          self.$refs.noti.warning("~",{
            timeout:2000
          });
          return 1;
        }else if ( !(/^\w{6,16}$/).exec(user_id)){
          self.$refs.noti.warning('6~16',{
            timeout:2000
          });
          return 1;
        }

        if( this.checkPsw() ){
          return;
        }
        if (this.id == ''){
          this.$refs.noti.warning("",{
            timeout:1500
          });
          return;
        }

        this.$refs.noti.noti('...',{
          timeout:0,
          bgColor:'blue'
        });

        return this.$request
        .post(`${this.$api.url_base}/logup`)
          .send({"user_id":user_id,"passwd":passwd, "school_id":school_id})
//          .then(this.$api.checkResult)
          .then(function (res) {
            if (res.body.code === 200){
              self.$refs.noti.noti('~',{
                timeout:1500,
                bgColor:'blue',
                callback(result,vm){
                  window.history.back();
                }
              });
            }else{
              this.$refs.noti.warning(JSON.stringify(res.body),{
                timeout:1500,
                bgColor:'red'
              });
            }

          })
      },
      clickTag() {
        this.$refs.tagEditor.showMe();
      }
    },
    components: {
      TagEditor,
      noti
    },
    computed: {}
  }
</script>



