<template>

  <div>
    <!---->
    <publish-picture :publish_text.sync="content.text" :image_publish.sync="content.file">
    </publish-picture>

    <!---->
    <publish-tag-choose :key="publish_key.tag" :tag.sync="content.lab_name" :tags="lab_items" :editable="!!1"></publish-tag-choose>
  </div>

  <noti v-ref:noti></noti>

</template>


<script type="text/ecmascript-6">

  import PublishPicture from '../../components/publish/publish-picture.vue'
  import PublishTagChoose from '../../components/publish/publish-tag-choose.vue'
  import {token, login_state, is_login} from '../../vuex/getters.js'
  import noti from '../../components/noti.vue'

  export default{
    route: {
      //()
      data(){
        this.content.lab_name = this.$route.query.topic;
        var token = this.token;
        return this.$request
          .get(`${this.$api.url_base}/ask_reply/labels?token=${token}`)
          .query({'ext_type':10,'limit':100})  // 5010
          .then(this.$api.checkResult)//codedata
          .then((data)=> {
            //
            this.lab_items = data.items.map(function(item,index){
              item.value = item.name;
              return item;
            });
          })
          .catch((e)=> {
            if (e.type === 'API_ERROR') {//apicheckResultcheckResult
              if (e.code === 23333) {//code,
                return this.$refs.noti.warning(``,{
                  timeout:1500
                })//return   : )
              } else if (e.code === 401) {
                return this.$router.go({
                  path: '/login',
                  query: {
                    __ref: this.$route.path//login
                  }
                });
              } else {
                return this.$refs.noti.warning(`:${e.message}`,{
                  timeout:1500
                })
              }
            } else {
              console.error(e.stack||e);
              return this.$refs.noti.warning(`:${e.message}`,{
                timeout:2000
              })
            }
            //
          })
      }
    },
    data(){
      return{
        content:{
          ext_type: 10,
          text: "",                  //"",
          file: [],                 //null,
          lab_name: "",               //
        },
        lab_items:[],
        publish_key:{
          tag:"# "
        },
        images:[
          {hasImage:false},
          {hasImage:false},
          {hasImage:false},
          {hasImage:false}
        ],
        has_add_image:true,
      }
    },
    methods:{
    },
    vuex: {
      actions: {},
      getters: {
        login_state,
        token,
        is_login
      }
    },
    events:{
      'cao-publish': function (message) {
        var self = this;
        let token = this.token;

        // 
        if (self.content.text.length == 0){
          self.$refs.noti.warning('~',{
            timeout:1500
          });
          return;
        }else if (self.content.lab_name===''){
          self.$refs.noti.warning('~',{
            timeout:1500
          });
          return;
        }

        if (message == "cao_in_publish") {
          var formData = new FormData();
          formData.append('ext_type',self.content.ext_type);
          formData.append("content", self.content.text);
          // 
          if (self.content.file.length > 0) {
            var imageCount = self.content.file.length > 4 ? 4 : self.content.file.length;
            for (var i = 0; i < imageCount; i++) {
              formData.append("file", self.content.file[i]);
            }
          }
          formData.append('lab_name',self.content.lab_name);

          this.$refs.noti.noti('...',{
            timeout:0,
            bgColor:'red'
          });

          return this.$request
            .post(`${this.$api.url_base}/ask_reply?token=${token}`)
            .send(formData)
            .then(this.$api.checkResult)//codedata
            .then(function () {
              self.$refs.noti.noti('~',{
                timeout:1500,
                bgColor:'red',
                callback(result,vm){
                  window.history.back();
                }
              });
            })
            .catch(function (err) {
              self.$refs.noti.warning(""+err,{
                timeout:1500
              });
            })
        }else {
          self.$refs.noti.warning('',{
            timeout:1500
          });
        }
      }
    },
    components:{
      PublishPicture,
      PublishTagChoose,
      noti
    }
  }
</script>



