<photos-wipe v-ref:viewer></photos-wipe>
<template>
  <div>
    <!--$loadingRouteDatatruefalse-->
    <div v-if="!$loadingRouteData">
      <photos-wipe v-ref:viewer></photos-wipe>
      <demand-card v-for="item in list" :data="item" :is_detail="false" @view-image="viewImage"
                   @click="$router.go('/demand-detail/'+item.id)"></demand-card>
      <!---->
      <infinite-loading :on-infinite="onLoadMore">
        <span slot="no-more">
          ...
        </span>
      </infinite-loading>
    </div>
    <!---->
    <div v-if="$loadingRouteData" class="loading-area">
      <pulse-loader color="rgb(38, 162, 255)" size="12px"></pulse-loader>
    </div>
  </div>
</template>
<!--CSS-->
<style scoped>
  .loading-area {
    display: flex;
    justify-content: center;;
    margin-top: 120px;
  }
</style>
<script type="text/ecmascript-6">
  import DemandCard from 'components/demand-card/demand-card.vue';
  import PhotosWipe from 'components/photoswipe/photoswipe.vue';
  import Vue from 'vue';
  //https://github.com/greyby/vue-spinner
  import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
  //https://peachscript.github.io/vue-infinite-loading
  import InfiniteLoading from 'vue-infinite-loading';
  import {token, login_state, is_login} from '../../vuex/getters.js'
  export default{
    components: {
      DemandCard,
      PhotosWipe,
      PulseLoader,
      InfiniteLoading
    },
    methods: {
      viewImage(index, photos){
        this.$refs.viewer.show(index, photos);
      },
      onLoadMore(){
        var token = this.token;
        this.$request
          .get(`${this.$api.url_base}/demand`)//GET url/demand
          .query({token: token})
          .query({ext_type: [4, 5]})
          .query({offset: this.data.offset + 5, limit: this.data.limit})
          .then(this.$api.checkResult)
          .then(data => {
            //  console.log(data);
            //
            this.$broadcast('$InfiniteLoading:loaded');
            //
            this.list = this.list.concat(data.items);
            this.data.offset = data.offset;
            this.data.total = data.total;
            //
            if (this.data.offset >= this.data.total) {
              this.$broadcast('$InfiniteLoading:complete');
            }
          })
          .catch((e)=> {
            if (e.type === 'API_ERROR') {//apicheckResultcheckResult
              if (e.code === 23333) {//code,
                return this.$refs.noti.warning(``, {
                  timeout: 1500
                })//return   : )
              } else if (e.code === 401) {
                return this.$router.go({
                  path: '/login',
                  query: {
                    __ref: this.$route.path//login
                  }
                });
              } else {
                return this.$refs.noti.warning(`:${e.message}`, {
                  timeout: 1500
                })
              }
            } else {
              console.error(e.stack || e);
              return this.$refs.noti.warning(`${e.message}`, {
                timeout: 1500
              })
            }
          })
      }
    },
    //
    route: {
      //()
      data(){
        var token = this.token;
        return this.$request
          .get(`${this.$api.url_base}/demand`)//GET url/demand
          .query({token: token})
          .query({ext_type: [4, 5]})//    query   url /demand?ext_type=1&ext_type=2&ext_type=3 
          .then(this.$api.checkResult)//codedata
          .then(function (data) {
            //vue-routerdata

            return {
              data: data,
              list: data.items
            }
          })
          .catch((e)=> {
              if (e.type === 'API_ERROR') {//apicheckResultcheckResult
              if (e.code === 23333) {//code,
                return this.$refs.noti.warning(``, {
                  timeout: 1500
                })//return   : )
              } else if (e.code === 401) {
                return this.$router.go({
                  path: '/login',
                  query: {
                    __ref: this.$route.path//login
                  }
                });
              } else {
                return this.$refs.noti.warning(`:${e.message}`, {
                  timeout: 1500
                })
              }
            } else {
              console.error(e.stack || e);
              return this.$refs.noti.warning(`${e.message}`, {
                timeout: 1500
              })
            }
          })
      }
    },
    data(){
      return {
        list: [],
        data: {}
      }
    },
    vuex: {
      actions: {},
      getters: {
        login_state,
        token,
        is_login
      }
    },

  }
</script>



