<template>

  <div class="publish">
    <div class="dating-content">
      <textarea class="content-text" maxlength=250 placeholder="" v-model="publish_text"></textarea>
    </div>

    <div class="add-img-wrapper" :style="imgWrapperHeight">

      <!---->
      <div class="add-img order2" v-for="image in images" v-show="image.hasImage" @click.stop="deletePhotos($index)" :style="{'background-image': 'url('+image.src+')'}">
      </div>
      <!---->
      <div class="add-img order20" v-if="has_add_image">
        <div class="dating-img add-img-top">
          <image-add></image-add>
        </div>
        <input id="fileUpload" class="add-img-bottom" v-on:change="addImage()" type="file"
               accept="image/jpeg, image/png, image/jpg, image/gif">
      </div>

    </div>

    <noti v-ref:noti></noti>

  </div>
</template>

<style scoped>
  .publish{
    margin: 0;
    font-size: 15px;
  }
  .dating-content{
    padding: 18px 18px 0 18px;
  }
  textarea{
    width: 100%;
    height: 150px;
    border-style: none;
    resize:none;
    outline: none;
  }
  .add-img-wrapper {
    padding: 2vw;
    display: flex;
    flex-wrap: nowrap;
    border-bottom: 1px solid #d5d5d5;
  }
  .add-img{
    position: relative;
    width:18vw;
    height: 18vw;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2vw;
  }
  .add-img-bottom{
    position: absolute;
    left: 0;
    top: 20px;
  }
  .order2{
    order:2;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border: 1vw solid #ffffff;
    box-shadow: 2px 2px 8px #E1E1E1;
    -webkit-box-shadow:2px 2px 8px #E1E1E1;
    -moz-box-shadow:2px 2px 8px #E1E1E1;
  }
  .order20{
    order:20;
    border: 1vw solid #f1f1f1;
  }
  .dating-img{
  }
  input{
    border-style: none;
    outline: none;
    padding-right: 20px;
    width:18vw;
    height: inherit;
    opacity: 0;
    filter: alpha(opacity=0);
  }
</style>

<script type="text/ecmascript-6">
  import ImageAdd from 'svg/common/ImageAdd.vue'
  import noti from '../../components/noti.vue'

  export default{

    data(){
      return{
        images:[
          {hasImage:false,src:""},
          {hasImage:false,src:""},
          {hasImage:false,src:""},
          {hasImage:false,src:""}
        ],
        has_add_image:true,
        index:-1  // onchange
      }
    },
    props:{
      publish_text:{
        type:String,
        default:""
      },
      image_publish:{
        type: Array,
        default:[]
      }
    },
    methods:{
      /**
       * 
       */
      deletePhotos(index){
        var self = this;
        this.$refs.noti.confirm('',{
          callback:function(result,vm){
            if(result){
              self.image_publish.splice(index,1);
              // images[index]item
              self.images.splice(index,1);
              self.index--;
              if (self.index<4){
                self.has_add_image = true;
              }
              self.images.push({
                hasImage:false,
                src:""
              })
            }
          }
        })
      },

      /**
       *    
       */

      addImage() {  // formData 
        var self = this;
        if (self.index < 4){

          console.log("index="+self.index);
          var ofileUpload = document.getElementById("fileUpload");   // 

          var pic_count = ofileUpload.files.length+self.index>3 ? (3-self.index) : ofileUpload.files.length;
          // 4index-13

          for(var i = 0; i < pic_count; i++){
            self.image_publish.push(ofileUpload.files[i]);
            console.log("self.image_publish"+self.image_publish[i]);
          }

          // 2
          var oFReader = new FileReader();
          oFReader.onload = function (oFREvent) {
            ++self.index;
            self.images[self.index].hasImage = true;
            self.images[self.index].src = oFREvent.target.result;
            if (self.index == 3){
              self.has_add_image = false;
            }
          };

          if (ofileUpload.files.length === 0) { return; }
          var oFile = ofileUpload.files[0];
          oFReader.readAsDataURL(oFile);

          /**
           *    2016.9.9 1108   
           */
          // 2,bug(oimagePreview index+1    index+pic_count )
//          function loadImageFile(start_index, end_index) {
//            if (ofileUpload.files.length === 0) {
//              return;
//            }else {
//              var oFile;
//              for (var tem_index = start_index; tem_index <= end_index; tem_index ++){
//                oFile = ofileUpload.files[tem_index];
//
//                var oFReader = new FileReader();
//                oFReader.readAsDataURL(oFile);
//                console.log("oFReader.readAsDataURL(oFile);");
//                oFReader.onload = function (oFREvent) {
//                  self.images[tem_index].hasImage = true;
//                  console.log(self.images[tem_index].hasImage);
//                  self.images[tem_index].src = oFREvent.target.result;
//                };
//              }
//            }
//          }
//          loadImageFile(index+1,index+pic_count);
//          index = index+pic_count;

        }else {
          console.log("4");
          return;
        }
      }
    },
    components:{
      ImageAdd,
      noti
    },
    events:{

    },
    computed:{
    }
  }
</script>



