<template>
    <div class="test">
        <div @click="clickTag()">{{tag || ''}}</div>
        <button @click="showChangePassword()"></button>
        <button @click="showMenu()"></button>
        <br>
        <button @click="showNoti('alert')">alert</button>
        <button @click="showNoti('confirm')">confirm</button>
        <button @click="showNoti('warning')">warning</button>
        <button @click="showNoti('noti')">noti</button>
        <tag-editor v-ref:tag-editor :tags.sync="tags" :tag.sync="tag" :editable="true" :show.sync="tagShow"></tag-editor>
        <noti v-ref:noti></noti>
        <!-- <fail text=""></fail> -->
        <change-password v-ref:changepassword></change-password>
        <menu v-ref:menu></menu>
    </div>
</template>
<script>
import fail from '../components/fail.vue'
import tagEditor from '../components/publish/publish-tag-editor.vue'
import menu from '../components/menu.vue'
import noti from '../components/noti.vue'
import changePassword from '../components/change-password.vue'
module.exports = {
    components: {
        tagEditor,
        noti,
        fail,
        changePassword,
        menu
    },
    methods: {
        showMenu() {
            this.$refs.menu.show({
                btns: [{
                    title: '1',
                    event: function() {
                        console.log('event1')
                        return true;
                    }
                }, {
                    title: '2',
                    event: function() {
                        console.log('event2')
                        return false;
                    }
                }, {
                    title: '3',
                    event: function() {
                        console.log('event3')
                        return true;
                    }
                }]
            })
        },
        showChangePassword() {
            this.$refs.changepassword.show({
                validator(oldPwd, newPwd1, newPwd2) {
                    console.log(oldPwd, newPwd1, newPwd2);
                    return newPwd1 === newPwd2 ? null : '';
                },
                confirm(oldPwd, newPwd, hide) {
                    alert(`:${oldPwd},:${newPwd}`);

                    setTimeout(function() {
                        hide();
                    }, 2000);
                }
            })
        },
        showNoti(type) {
            var that = this;
            switch (type) {
                case 'alert':
                    this.$refs.noti.alert('', {
                        bgColor: 'green',
                        callback(result, vm) {
                            console.log('alert', result, vm);
                        }
                    });
                    break;
                case 'confirm':
                    this.$refs.noti.confirm('CONFIRM', {
                        callback(result, vm) {
                            console.log('confirm', result);
                            if (result) vm.alert('!');
                            else vm.warning('!', {
                                timeout: 2000
                            });
                        }
                    });
                    break;
                case 'warning':
                    this.$refs.noti.warning('WARNING!', {
                        callback(result, vm) {
                            console.log('warning', result, vm);
                        }
                    });
                    break;
                case 'noti':
                    this.$refs.noti.noti('NOTI!!!!23333333333333sdagasdgasddsagasdgsagssadgsadgasdgasdgs');
                    break;
            }
        },
        clickTag() {
            this.$refs.tagEditor.showMe();
        }
    },
    data() {
        return {
            tag: '',
            tags: [{
                "name": "",
                "value": 123
            }, {
                "name": "",
                "value": 321
            }, {
                "name": "",
                "value": 333
            }, {
                "name": "",
                "value": 111
            }, {
                "name": "2",
                "value": 222
            }, {
                "name": "2",
                "value": 1221213
            }, {
                "name": "2",
                "value": 123
            }, {
                "name": "2",
                "value": 123
            }, {
                "name": "3",
                "value": 123
            }, {
                "name": "3",
                "value": 123
            }, {
                "name": "3",
                "value": 123
            }, {
                "name": "3",
                "value": 123
            }, {
                "name": "4",
                "value": 123
            }, {
                "name": "4",
                "value": 123
            }, {
                "name": "4",
                "value": 123
            }, {
                "name": "4",
                "value": 123
            }],
            tagShow: false
        }
    }
}
</script>



